* ul,
* li {
    list-style: none;
}

:root {
    --CorText: rgb(201, 129, 216);

    --Cor: #985803;
    --Cor_Shadow: #452803;

    --CorNav: rgba(94, 12, 110, 0.4);
    --CorNav_Shadow: #360340;

    --CorUl: rgba(143, 33, 0, 0.8);
    --CorDefault: #b40000;
}

header {
    position: fixed;
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;

    width: 100%;
    height: 80px;


    &>div:nth-child(1) {
        background-color: var(--Cor);
        box-shadow: 4px 2px 2px var(--Cor_Shadow);


        z-index: 20;
    }

    &>div:nth-child(2) {
        height: auto;
        width: 100%;
        min-height: 60px;


        background-color: var(--CorNav);
        box-shadow: 4px 2px 2px var(--CorNav_Shadow);

        z-index: 10;
        backdrop-filter: blur(4px);
        opacity: .9;
    }

}

.AsideNav {
    width: 100%;

    &>nav {
        &>div:first-child {
            max-height: 40px;

            buttom {
                transition: rotate 4s;
            }
        }

        ul {
            overflow-x: scroll;


            background-color: var(--CorUl);
            scrollbar-darkshadow-color: var(--Cor_Shadow);
            scrollbar-color: rgb(77, 47, 1) rgba(199, 113, 1, 0.42);

            &>li {
                background-color: black;
                margin: 5px 40px;

                * {
                    border: none;
                }

                &,
                &>button,
                &>button>img {
                    width: 180px;
                    height: 100px;
                }

                &>button {
                    display: flex;
                    background: var(--CorDefault);
                    color: var(--CorText);

                    justify-content: center;
                    align-items: flex-end;


                    &>img {
                        position: absolute;
                        top: 0;
                        left: 0;

                        width: 100%;
                        height: 100%;
                        object-fit: cover;
                        z-index: -1;
                    }
                }
            }

        }
    }
}

.GenLi {
    box-shadow: 0 2px 4px 2px var(--CorNav_Shadow);
}

.FocusLi {
    position: relative;
    z-index: 0;
    
    &::before {
        content: "";
        position: absolute;
        
        width: 100%;
        height: 100%;

        top: 0;
        left: 0;
        
        background: 
        radial-gradient(circle, rgba(135, 234, 255, 1) 0%, rgba(255, 205, 0, 1) 18%, rgba(10, 188, 193, 1) 36%, rgba(0, 43, 196, 1) 52%, rgba(204, 87, 8, 1) 72%, rgba(1, 95, 96, 1) 88%, rgba(135, 234, 255, 1) 100%);

        background-size: 800%;
        border-radius: 10px;
        filter: blur(8px);

        animation: AnimButtons 20s linear infinite alternate;

        z-index: -1;
    }
}

@keyframes AnimButtons {
    0% {
        background-position: 0 0;
    }
    50% {
        background-position: 400% 0;
    }
    100%{
        background-position: 0 0;
    }
}